import * as Cesium from "cesium/Cesium";

function selectOnMap(subject = null) {
  console.warn(
    "Caution, 'MapController.selectOnMap()' is a beta feature and is considered unstable.",
  );
  window.viewer.entities.removeById("_CoreMapsSelection");
  if (this.shouldPickEntity === true) {
    if (subject == null) {
      window.viewer.selectedEntity = null;
    } else if (subject.constructor.name === "Entity") {
      window.viewer.selectedEntity = subject;
    } else {
      let position;
      position = Cesium.Cartesian3.clone(subject.position, position);
      const selection = window.viewer.entities.add({
        id: "_CoreMapsSelection",
        name: "_CoreMapsSelection",
        position: position,
      });
      window.viewer.selectedEntity = selection;
    }
  }
}

function selectEntity(entity = null) {
  if (this.shouldPickEntity === true) {
    window.viewer.selectedEntity = entity;
  }
}

function selectEntityById(id = null) {
  if (!id) {
    this.selectEntity(null);
    return;
  }
  const entity = window.viewer.entities.getById(id);
  this.selectEntity(entity);
  return entity;
}

function selectEntityFromDataSourceById(dataSourceName, id = null) {
  if (!id) {
    this.selectEntity(null);
    return;
  }
  const dataSource = window.viewer.dataSources.getByName(dataSourceName)[0];
  const entity = dataSource.entities.getById(id);
  this.selectEntity(entity);
  return entity;
}

function getEntityById(id) {
  for (const dataSource in window.viewer.dataSources._dataSources) {
    const entity = dataSource.entities.getById(id);
    if (entity) return entity;
  }
  return null;
}

function getRawDataById(id, from) {
  for (const dataController in this._dataControllers) {
    const raw = dataController.getSpatialRawData(id, from);
    if (raw) return raw;
  }
  return null;
}

function getAllEntities() {
  let entities = [];
  window.viewer.dataSources._dataSources.forEach(dataSource => {
    entities = entities.concat(dataSource.entities.values);
  });
  return entities;
}

/**
 *
 * Register a Cesium [ScreenSpaceEvent](https://cesium.com/learn/cesiumjs/ref-doc/ScreenSpaceEventHandler.html?classFilter=ScreenSpace) with a given callback.
 * Clicked position will be processed with default picker or dirllpicker
 * @param {*} callback
 * @param {string} [eventType="LEFT_CLICK"]
 * @param {*} [pickerMode=undefined]
 * @param {*} [options={ limit: undefined, width: 3, height: 3 }]
 */
function registerInteractCallback(
  callback,
  eventType = "LEFT_CLICK",
  pickerMode = undefined,
  options = { limit: undefined, width: 3, height: 3 },
) {
  const handler = new Cesium.ScreenSpaceEventHandler(
    window.viewer.scene.canvas,
  );
  let inputAction = event => {
    callback(event);
  };
  switch (pickerMode) {
    case "pick":
      inputAction = event => {
        const objects = window.viewer.scene.pick(event.position);
        callback(event, objects);
      };
      break;
    case "drillpick":
      inputAction = event => {
        const objects = window.viewer.scene.drillPick(
          event.position,
          options.limit,
          options.width,
          options.height,
        );
        callback(event, objects);
      };
      break;
    default:
      break;
  }
  handler.setInputAction(inputAction, Cesium.ScreenSpaceEventType[eventType]);
}

export {
  registerInteractCallback,
  selectOnMap,
  selectEntity,
  selectEntityById,
  selectEntityFromDataSourceById,
  getEntityById,
  getRawDataById,
  getAllEntities,
};
