import * as Cesium from "cesium/Cesium";

const mins = [
  Cesium.Math.toRadians(0.0005),
  Cesium.Math.toRadians(0.001),
  Cesium.Math.toRadians(0.005),
  Cesium.Math.toRadians(0.01),
  Cesium.Math.toRadians(0.05),
  Cesium.Math.toRadians(0.1),
  Cesium.Math.toRadians(0.2),
  Cesium.Math.toRadians(0.5),
  Cesium.Math.toRadians(1.0),
  Cesium.Math.toRadians(2.0),
  Cesium.Math.toRadians(5.0),
  Cesium.Math.toRadians(10.0),
];

/**
 *
 * 1:1 raito graticule.
 * Original author - Thomas Lefort on 27/01/14. Published with Apache License, Version 2.0.
 * https://github.com/leforthomas/cesium-addons/blob/master/Graticule.js
 * @todo Simplify the logic where renders meridian
 * @class Graticule
 */
class Graticule {
  constructor(description) {
    this.viewer = window.viewer;

    const scene = this.viewer.scene;

    description = description || {};
    this._tilingScheme =
      description.tilingScheme || new Cesium.GeographicTilingScheme();
    this._color = description.color || new Cesium.Color(1.0, 1.0, 1.0, 0.4);
    this._tileWidth = description.tileWidth || 256;
    this._tileHeight = description.tileHeight || 256;

    this._ready = true;

    // default to decimal intervals
    this._sexagesimal = description.sexagesimal || false;
    this._numLines = description.numLines || 50;

    this._scene = scene;
    this._labels = new Cesium.LabelCollection();
    scene.primitives.add(this._labels);
    this._polylines = new Cesium.PolylineCollection();
    scene.primitives.add(this._polylines);
    this._ellipsoid = scene.globe.ellipsoid;

    const canvas = document.createElement("canvas");
    canvas.width = 256;
    canvas.height = 256;
    this._canvas = canvas;

    this.defineProperties = Object.defineProperties;

    this.eventRemoveCallBack = [];
  }

  addUpdateLisenter() {
    const events = ["moveEnd", "changed"];
    events.forEach(event => {
      const camEvent = this.viewer.camera[event];
      let eventRemover = camEvent.addEventListener(() => {
        this.hide();
        this.show();
      });
      this.eventRemoveCallBack.push(eventRemover);
    });
    const camEvent = this.viewer.scene.preRender;
    let eventRemover = camEvent.addEventListener(() => {
      this.hide();
      this.show();
    });
    this.eventRemoveCallBack.push(eventRemover);
  }

  removeUpdateLisenter() {
    this.eventRemoveCallBack.forEach(eventRemover => {
      eventRemover();
    });
  }

  makeLabel(lng, lat, text, top, color) {
    this._labels.add({
      position: this._ellipsoid.cartographicToCartesian(
        new Cesium.Cartographic(lng, lat, 10.0),
      ),
      text: text,
      font: "15px Sans-serif",
      fillColor: Cesium.Color.WHITE,
      outlineColor: Cesium.Color.WHITE,
      outlineWidth: 0,
      style: Cesium.LabelStyle.FILL,
      pixelOffset: new Cesium.Cartesian2(10, top ? 20 : -20),
      eyeOffset: Cesium.Cartesian3.ZERO,
      horizontalOrigin: Cesium.HorizontalOrigin.LEFT,
      verticalOrigin: top
        ? Cesium.VerticalOrigin.BOTTOM
        : Cesium.VerticalOrigin.TOP,
      scale: 1.0,
    });
  }

  _drawGrid(extent) {
    if (this._currentExtent && this._currentExtent.equals(extent)) {
      return;
    }
    this._currentExtent = extent;

    this._polylines.removeAll();
    this._labels.removeAll();

    // TODO: OPTIMIZE LOGIC

    let dLat = 0,
      dLng = 0,
      index;
    // check if camera includes meridian.
    if (
      !(
        extent.west > 0 &&
        extent.east < 0 &&
        extent.west - extent.east >
          Math.PI - extent.west + (Math.PI + extent.east)
      )
    ) {
      for (
        index = 0;
        index < mins.length && dLng < (extent.east - extent.west) / 10;
        index++
      ) {
        dLat = mins[index];
        dLng = mins[index];
      }

      // round iteration limits to the computed grid interval
      let minLng =
        (extent.west < 0
          ? Math.ceil(extent.west / dLng)
          : Math.floor(extent.west / dLng)) * dLng;
      let minLat =
        (extent.south < 0
          ? Math.ceil(extent.south / dLat)
          : Math.floor(extent.south / dLat)) * dLat;
      let maxLng =
        (extent.east < 0
          ? Math.ceil(extent.east / dLat)
          : Math.floor(extent.east / dLat)) * dLat;
      let maxLat =
        (extent.north < 0
          ? Math.ceil(extent.north / dLng)
          : Math.floor(extent.north / dLng)) * dLng;

      // extend to make sure we cover for non refresh of tiles
      minLng = Math.max(minLng - 2 * dLng, -Math.PI);
      maxLng = Math.min(maxLng + 2 * dLng, Math.PI);
      minLat = Math.max(minLat - 2 * dLat, -Math.PI / 2);
      maxLat = Math.min(maxLat + 2 * dLng, Math.PI / 2);

      let ellipsoid = this._ellipsoid;
      let lat,
        lng,
        granularity = Cesium.Math.toRadians(1);

      // labels positions
      const latitudeText =
        minLat + Math.floor((maxLat - minLat) / dLat / 2) * dLat;
      for (lng = minLng; lng < maxLng; lng += dLng) {
        // draw meridian
        let path = [];
        for (lat = minLat; lat < maxLat; lat += granularity) {
          path.push(new Cesium.Cartographic(lng, lat));
        }
        path.push(new Cesium.Cartographic(lng, maxLat));
        this._polylines.add({
          positions: ellipsoid.cartographicArrayToCartesianArray(path),
          width: 1.5,
          material: this._generatePolylineMaterial(),
        });
        const degLng = Cesium.Math.toDegrees(lng);
        if (Math.abs(Math.round(degLng)) != 180)
          this.makeLabel(
            lng,
            latitudeText,
            (this._sexagesimal
              ? this._decToSex(degLng)
              : degLng.toFixed(this.gridPrecision(dLng))) +
              (degLng >= 0.0 ? "E" : "W"),
            false,
          );
        else
          this.makeLabel(
            lng,
            latitudeText,
            this._sexagesimal
              ? this._decToSex(degLng)
              : degLng.toFixed(this.gridPrecision(dLng)),
            false,
          );
      }

      // lats
      const longitudeText =
        minLng + Math.floor((maxLng - minLng) / dLng / 2) * dLng;
      for (lat = minLat; lat < maxLat; lat += dLat) {
        // draw parallels
        let path = [];
        for (lng = minLng; lng < maxLng; lng += granularity) {
          path.push(new Cesium.Cartographic(lng, lat));
        }
        path.push(new Cesium.Cartographic(maxLng, lat));
        this._polylines.add({
          positions: ellipsoid.cartographicArrayToCartesianArray(path),
          width: 1.5,
          material: this._generatePolylineMaterial(),
        });
        const degLat = Cesium.Math.toDegrees(lat);
        this.makeLabel(
          longitudeText,
          lat,
          (this._sexagesimal
            ? this._decToSex(degLat)
            : degLat.toFixed(this.gridPrecision(dLat))) +
            (degLat >= 0.0 ? "N" : "S"),
          true,
        );
        this.makeLabel(
          Math.PI,
          lat,
          (this._sexagesimal
            ? this._decToSex(degLat)
            : degLat.toFixed(this.gridPrecision(dLat))) +
            (degLat >= 0.0 ? "N" : "S"),
          true,
        );
      }
    } else {
      // get the nearest to the calculated value
      for (
        index = 0;
        index < mins.length && dLat < (extent.north - extent.south) / 10;
        index++
      ) {
        dLat = mins[index];
      }
      for (
        index = 0;
        index < mins.length &&
        dLng < (2 * Math.PI - extent.west + extent.east) / 10;
        index++
      ) {
        dLng = mins[index];
      }
      // round iteration limits to the computed grid interval
      let minLng =
        (extent.west < 0
          ? Math.ceil(extent.west / dLng)
          : Math.floor(extent.west / dLng)) * dLng;
      let minLat =
        (extent.south < 0
          ? Math.ceil(extent.south / dLat)
          : Math.floor(extent.south / dLat)) * dLat;
      let maxLng =
        (extent.east < 0
          ? Math.ceil(extent.east / dLat)
          : Math.floor(extent.east / dLat)) * dLat;
      let maxLat =
        (extent.north < 0
          ? Math.ceil(extent.north / dLng)
          : Math.floor(extent.north / dLng)) * dLng;

      // extend to make sure we cover for non refresh of tiles
      minLng = Math.max(minLng - 2 * dLng, -Math.PI);
      maxLng = Math.min(maxLng + 2 * dLng, Math.PI);
      minLat = Math.max(minLat - 2 * dLat, -Math.PI / 2);
      maxLat = Math.min(maxLat + 2 * dLng, Math.PI / 2);

      let ellipsoid = this._ellipsoid;
      let lat,
        lng,
        granularity = Cesium.Math.toRadians(1);

      // labels positions
      const latitudeText =
        minLat + Math.floor((maxLat - minLat) / dLat / 2) * dLat;
      for (lng = -Math.PI; lng < minLng; lng += dLng) {
        // draw meridian
        let path = [];
        for (lat = minLat; lat < maxLat; lat += granularity) {
          path.push(new Cesium.Cartographic(lng, lat));
        }
        path.push(new Cesium.Cartographic(lng, maxLat));
        this._polylines.add({
          positions: ellipsoid.cartographicArrayToCartesianArray(path),
          width: 1.5,
          material: this._generatePolylineMaterial(),
        });
        const degLng = Cesium.Math.toDegrees(lng);
        this.makeLabel(
          lng,
          latitudeText,
          (this._sexagesimal
            ? this._decToSex(degLng)
            : degLng.toFixed(this.gridPrecision(dLng))) +
            (degLng >= 0.0 ? "E" : "W"),
          false,
        );
      }
      for (lng = Math.PI - dLng; lng > maxLng; lng -= dLng) {
        // draw meridian
        let path = [];
        for (lat = minLat; lat < maxLat; lat += granularity) {
          path.push(new Cesium.Cartographic(lng, lat));
        }
        path.push(new Cesium.Cartographic(lng, maxLat));
        this._polylines.add({
          positions: ellipsoid.cartographicArrayToCartesianArray(path),
          width: 1.5,
          material: this._generatePolylineMaterial(),
        });
        const degLng = Cesium.Math.toDegrees(lng);
        this.makeLabel(
          lng,
          latitudeText,
          (this._sexagesimal
            ? this._decToSex(degLng)
            : degLng.toFixed(this.gridPrecision(dLng))) +
            (degLng >= 0.0 ? "E" : "W"),
          false,
        );
      }

      // lats
      let center = extent.east - (2 * Math.PI - extent.west + extent.east) / 2;
      const longitudeText =
        Math.floor((center < -Math.PI ? center : center + 2 * Math.PI) / dLng) *
        dLng;
      for (lat = minLat; lat < maxLat; lat += dLat) {
        // draw parallels
        let path = [];
        for (lng = Math.PI; lng > maxLng; lng -= granularity) {
          path.push(new Cesium.Cartographic(lng, lat));
        }
        path.push(new Cesium.Cartographic(maxLng, lat));
        this._polylines.add({
          positions: ellipsoid.cartographicArrayToCartesianArray(path),
          width: 1.5,
          material: this._generatePolylineMaterial(),
        });
        const degLat = Cesium.Math.toDegrees(lat);
        this.makeLabel(
          longitudeText,
          lat,
          (this._sexagesimal
            ? this._decToSex(degLat)
            : degLat.toFixed(this.gridPrecision(dLat))) +
            (degLat >= 0.0 ? "N" : "S"),
          true,
        );
        path = [];
        for (lng = -Math.PI; lng < minLng; lng += granularity) {
          path.push(new Cesium.Cartographic(lng, lat));
        }
        path.push(new Cesium.Cartographic(minLng, lat));
        this._polylines.add({
          positions: ellipsoid.cartographicArrayToCartesianArray(path),
          width: 1.5,
          material: this._generatePolylineMaterial(),
        });
        this.makeLabel(
          longitudeText,
          lat,
          (this._sexagesimal
            ? this._decToSex(degLat)
            : degLat.toFixed(this.gridPrecision(dLat))) +
            (degLat >= 0.0 ? "N" : "S"),
          true,
        );
      }
    }
  }

  _generatePolylineMaterial() {
    return Cesium.Material.fromType("Color", {
      color: new Cesium.Color(1.0, 1.0, 1.0, 0.4),
    });
  }

  requestImage(x, y, level) {
    if (this._show) {
      this._drawGrid(this._getExtentView());
    }
    return this._canvas;
  }

  /**
   *
   * Set the visibleity of the graticule
   * @param {boolean} visible
   * @memberof Graticule
   */
  setVisible(visible) {
    this._show = visible;
    if (!visible) {
      this._polylines.removeAll();
      this._labels.removeAll();
      this.removeUpdateLisenter();
    } else {
      this._currentExtent = null;
      this._drawGrid(this._getExtentView());
      this.addUpdateLisenter();
    }
  }

  /**
   *
   * Show the graticule
   * @memberof Graticule
   */
  show() {
    this.setVisible(true);
  }

  /**
   *
   * Hide the graticule
   * @memberof Graticule
   */
  hide() {
    this.setVisible(false);
  }

  /**
   *
   * Check if the graticule is visible
   * @return {*}
   * @memberof Graticule
   */
  isVisible() {
    return this._show;
  }

  _decToSex(d) {
    let degs = Math.floor(d);
    let mins = ((Math.abs(d) - degs) * 60.0).toFixed(2);
    if (mins == "60.00") {
      degs += 1.0;
      mins = "0.00";
    }
    return [degs, ":", mins].join("");
  }

  _getExtentView() {
    const camera = this._scene.camera;
    const canvas = this._scene.canvas;
    let corners = [
      camera.pickEllipsoid(new Cesium.Cartesian2(0, 0), this._ellipsoid),
      camera.pickEllipsoid(
        new Cesium.Cartesian2(canvas.width, 0),
        this._ellipsoid,
      ),
      camera.pickEllipsoid(
        new Cesium.Cartesian2(0, canvas.height),
        this._ellipsoid,
      ),
      camera.pickEllipsoid(
        new Cesium.Cartesian2(canvas.width, canvas.height),
        this._ellipsoid,
      ),
    ];
    for (var index = 0; index < 4; index++) {
      if (corners[index] === undefined) {
        return Cesium.Rectangle.MAX_VALUE;
      }
    }
    return Cesium.Rectangle.fromCartographicArray(
      this._ellipsoid.cartesianArrayToCartographicArray(corners),
    );
  }

  gridPrecision(dDeg) {
    if (dDeg < 0.001) return 4;
    if (dDeg < 0.01) return 3;
    if (dDeg < 0.1) return 2;
    if (dDeg < 1) return 1;
    return 0;
  }

  get url() {
    return undefined;
  }

  get proxy() {
    return undefined;
  }

  get tileWidth() {
    return this._tileWidth;
  }

  get tileHeight() {
    return this._tileHeight;
  }

  get maximumLevel() {
    return 18;
  }

  get minimumLevel() {
    return 0;
  }
  get tilingScheme() {
    return this._tilingScheme;
  }
  get rectangle() {
    return this._tilingScheme.rectangle;
  }
  get tileDiscardPolicy() {
    return undefined;
  }
  get errorEvent() {
    return this._errorEvent;
  }
  get ready() {
    return this._ready;
  }
  get credit() {
    return this._credit;
  }
  get hasAlphaChannel() {
    return true;
  }

  destroy() {
    this.removeUpdateLisenter();
    for (const key of Object.getOwnPropertyNames(this)) {
      this[key] = null;
    }
  }
}

export default Graticule;
