/**
 * Basic Cesium object controller, it might be entity, or primitive.
 * @param entityType {string} A name for the data source.
 */
class BaseCesiumSpatial {
  constructor(entityType = "BaseCesiumObject", isPickable = true) {
    if (entityType == "BaseCesiumObject") {
      console.warn(
        `entityType should be decleared in class "${this.constructor.name}"`,
      );
    }
    if (!window.viewer) {
      throw new Error("No viewer has been initialized.");
    }
    this.viewer = window.viewer;
    this._entityType = entityType;
    this.isPickable = isPickable;
    this._idList = [];
    this.filters = [];
    this.filterSetsVisibility = true;
    this.filterMode = "UNION";

    // DEPRECATED reserved for backward capability, will be removed in the future.
    this.__raw = {};
    this._raw = new Proxy(this.__raw, {
      set: (target, key, value) => {
        console.debug(
          "'_raw' is a deprecated internal variable, and it is reserved for backward capbility only, please use entity.description or primitive.description instead.",
        );
        target[key] = { ...value };
        return true;
      },
      get: (target, key) => {
        console.debug(
          "'_raw' is a deprecated internal variable, and it is reserved for backward capbility only, please use entity.description or primitive.description instead.",
        );
        return target[key];
      },
    });
  }

  add(id, coords, options = {}) {
    if (this._idList.includes(id)) {
      return this.update(id, coords, options);
    }
  }

  update(id, coords, options = {}) {
    if (this._idList.includes(id)) {
      return this.add(id, coords, options);
    }
  }

  /**
   * Remove all existing entity points.
   */
  removeAll() {
    this._idList = [];
  }
  /**
   * A function to remove entity point by a given ID.
   * @param id {string} Entity ID
   */
  remove(id) {
    const index = this._idList.indexOf(id);
    if (index > -1) {
      this._idList.splice(index, 1);
    }
  }

  /**
   * @param by {string} A field or attribute to trigger visibility of the entity.
   * @param cmp {function} A custom compare function for the trigger. If the cmpare function is not provided, the filter() function will check if the entity's attribute provided by the parameter "by" exists, or its boolean value by default.
   * @param setVisibility {boolean} if the spatial needs to set object's visibility
   */
  filter(by = null, cmp = undefined, setVisibility = true) {
    console.warn(
      `Method filter should be implemented in class "${this.constructor.name}".`,
    );
  }

  _filter(target, targetID, cmp = undefined) {
    let results = [];
    cmp.forEach(cmpFn => {
      results.push(cmpFn(target, targetID));
    });
    return results;
  }

  _calcFilterResult(result = [], mode = "UNION") {
    if (mode === "UNION") {
      return result.includes(true);
    } else if (mode === "INTERSECTION") {
      return result.filter(v => v === true).length === result.length;
    } else {
      throw new Error(`Mode ${mode} does not exist`);
    }
  }

  /**
   * Acquire spatial's description data by id.
   * @param id {*} The entity/primitive id.
   */
  getSpatialDescription(id) {
    throw new Error(
      `Method getSpatialDescription() should be implemented in class "${this.constructor.name}".`,
    );
  }

  /**
   * Set spatial's description data by id.
   * @param id {*} The entity/primitive id.
   */
  setSpatialDescription(id, description) {
    throw new Error(
      `Method setSpatialDescription() should be implemented in class "${this.constructor.name}".`,
    );
  }

  /**
   * @deprecated use `getSpatialDescription()` instead.
   * Acquire spatial's raw data by id.
   * @param id {*} The entity/primitive id.
   */
  getRawSpatialData(id) {
    return this._raw ? this._raw[id] : {};
  }

  /**
   * @deprecated use `getSpatialDescription()` instead.
   * Acquire spatial's raw data by id.
   * @param id {*} The entity id.
   */
  getRaw(id) {
    console.warn(
      "Method 'getRaw' is depercated, please use 'getRawSpatialData' instead.",
    );
    return this.getRawSpatialData(id);
  }

  /**
   * Acquire all id in this object.
   * @param id {*} The entity id.
   */
  getIdList() {
    return this._idList;
  }

  /**
   * @deprecated use `getIdList()` instead.
   * Acquire all id in this object.
   * @param id {*} The entity id.
   */
  getIDs() {
    return this.getIdList();
  }

  destroy() {
    for (const key of Object.getOwnPropertyNames(this)) {
      this[key] = null;
    }
  }
}

export default BaseCesiumSpatial;
