import { parseFields } from "../Utils/helper";

/**
 * Polling, responsible for fetching data repeatly with a given interval.
 * @param api {function} The API
 * @param spatial {function} The render method for polling
 * @param schema {object} An object contains parameters for polling's update [See more about "schema"](http://192.168.100.236:3000/s/OLBwpZHB6)
 * @param apiParameters {array} Additional API parameters
 * @param interval {number} The interval for polling in milliseconds, default is 5000 (5s).
 * @param callback {function} An optional callback function which will be called at the end of each polling.
 */
class Polling {
  constructor(
    api,
    spatial,
    schema,
    apiParameters = null,
    interval = 5000,
    callback = undefined,
  ) {
    this.api = api;
    this.spatial = spatial;
    this.schema = schema;
    this.apiParameters = apiParameters;
    this.interval = interval;
    this.isPolling = false;
    this.callback = callback;
    this._lastUpdate = new Object();
    this._idList = new Array();
  }

  /**
   *
   * Start the polling
   * @return {*}
   * @memberof Polling
   */
  async start() {
    if (this.isPolling) {
      console.warn("Already polling.");
      return;
    }
    this.isPolling = true;
    await this._poll();
    this.intervalID = window.setInterval(() => {
      this._poll();
    }, this.interval);
  }

  /**
   *
   * Stop the polling
   * @return {*}
   * @memberof Polling
   */
  stop() {
    if (!this.isPolling) {
      console.warn("Polling cannot be stopped since it hasn't been started.");
      return;
    }
    this.isPolling = false;
    window.clearInterval(this.intervalID);
  }

  async _poll() {
    // api should return a json object in the future.
    const objects = await this.api(this.apiParameters);
    // Support for fetch API, which will be deprecated soon.
    // const objects = await response.json();

    // Prevent accidentally updates when the _poll callback returns pops from the call stack withoit knowing the polling is over.
    if (!this.isPolling) {
      return;
    }

    // Support for axios, which will be deprecated soon.
    // const objects = response.data;

    let updated = Array();
    this._idList = [];
    objects.forEach(object => {
      let [id, coords, options, timestamp] = parseFields(object, this.schema);
      this._idList.push(id);

      if (!this._lastUpdate[id])
        this.spatial.add(id, [...coords, timestamp ?? 0], options);
      else if (timestamp > this._lastUpdate[id])
        this.spatial.update(id, [...coords, timestamp ?? 0], options);
      else return;

      this._lastUpdate[id] = timestamp;
      updated.push(id);
    });
    if (this.callback) this.callback(updated);
  }

  getIDs() {
    return this._idList;
  }
}

export default Polling;
