<template>
  <v-app>
    <div id="app">
      <div id="viewer"></div>
      <windowManager>
        <Window 
          v-for="i in 2" 
          :key="i" 
          :ref="`window-${i}`"
          :title="`Window${i}`"
          :default-y="`${20 * (i - 1)}%`"
          :default-x="`${20 * (i - 1)}%`"
          min-width="25%"
          max-width="20%"
        >
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec elementum condimentum efficitur. 
        </Window>
        <Window 
          ref="window-3"
          :default-y="`${20 * 2}%`"
          :default-x="`${20 * 2}%`"
          min-width="25%"
          max-width="20rem"
          min-height="25%"
          :hide-default-header="true"
        >
          <template slot="header">
            <div class="custom-header">
              Window 3 with custom header
              <span class="actions" @click="minimizeWindow(3)"> MINIMIZE </span>
              <span class="actions" @click="closeWindow(3)"> CLOSE </span>
            </div>
          </template>
          This Example shows how to the vue-x11 looks like, and in the back is an upcoming project named <a href="https://github.com/CoreMaps" target="_blank">CoreMaps</a> that makes CesiumJS even better and quicker to develop, the project is currentlly under development, stay tuned!
        </Window>
      </windowManager>
    </div>
    <div class="scale-container" id="scale-container">
      <div class="scale-value" id="scale-value">Loading...</div>
      <div class="scale-bar" id="scale-bar"></div>
    </div>
    <div class="coords">
      <table style="width:100%">
          <div>{{ coords.split(",")[0] }}</div>
          <div>{{ coords.split(",")[1] }}</div>
          <div>{{ coordsDMS.split(",")[0] }}</div>
          <div>{{ coordsDMS.split(",")[1] }}</div>
      </table>
    </div>
    <div class="version">{{ coreMapsVersion }}</div>
  </v-app>
</template>

<script>
import Window from "./components/Window.vue";
import windowManager from "./components/WindowManager.vue"

import * as CoreMaps from "@/CoreMaps";
import MapConfig from "./MapConfig";
import "cesium/Widgets/widgets.css";

export default {
  name: 'App',
  components: {
    Window, 
    windowManager,  
  }, 
  data() {
    return {
      coords: ',', 
      coordsDMS: ',', 
      coreMapsVersion: null, 
    }
  },
  methods: {
    minimizeWindow(i){
      console.log(this.$refs);
      console.log(`window-${i}`);
      this.$refs[`window-${i}`].toggleMinimize();
    }, 
    closeWindow(i){
      this.$refs[`window-${i}`].toggleVisibility();
    }, 
    createViewer() {
      this.coreMapsVersion = CoreMaps.getVersion();
      
      CoreMaps.init(
        "viewer", 
        process.env.VUE_APP_CESIUM_TOKEN, 
        [], 
        MapConfig.viewerOptions, 
        MapConfig.settings, 
        MapConfig.features, 
      );
      CoreMaps.MapController.CameraControl.watchCurrentMouseCoords((event) => {
        if(event.coords){
        this.coords = `${event.coords.longitude}, ${event.coords.latitude}`
        function convertToDms(dd, isLng) {
          let dir = dd < 0 ? isLng ? 'W' : 'S' : isLng ? 'E' : 'N';

          let absDd = Math.abs(dd);
          let deg = absDd | 0;
          let frac = absDd - deg;
          let min = (frac * 60) | 0;
          let sec = frac * 3600 - min * 60;
          // Round it to 2 decimal points.
          sec = Math.round(sec * 100) / 100;
          return ("0" + deg).slice(-2) + "°" + ("0" + min).slice(-2) + "'" + ("0" + sec).slice(-2) + '"' + ("0" + dir).slice(-2);
        }
        this.coordsDMS = `${convertToDms(event.coords.longitude, true)}, ${convertToDms(event.coords.latitude, false)}`;
        }
      })
    },
  },
  mounted() {
    this.createViewer();
  },
}
</script>

