import * as Cesium from "cesium/Cesium";
// import GIBS from "./gibs";

function loadStyle(viewer) {
  const frame = viewer.infoBox.frame;
  frame.addEventListener(
    "load",
    function() {
      var cssLink = frame.contentDocument.createElement("link");
      cssLink.href = Cesium.buildModuleUrl("./SrcAssets/description.css");
      cssLink.rel = "stylesheet";
      cssLink.type = "text/css";
      frame.contentDocument.head.appendChild(cssLink);
    },
    false,
  );
}

function createSkyBox(baseUrl) {
  return new Cesium.SkyBox({
    sources: {
      positiveX: `${baseUrl}px.jpg`,
      negativeX: `${baseUrl}nx.jpg`,
      positiveY: `${baseUrl}py.jpg`,
      negativeY: `${baseUrl}ny.jpg`,
      positiveZ: `${baseUrl}pz.jpg`,
      negativeZ: `${baseUrl}nz.jpg`,
    },
  });
}

export function createCesiumViewer(
  container,
  options = {},
  GISPlatformToken = null,
  imageryProviders = [],
) {
  Cesium.Ion.defaultAccessToken = null;
  if (options.focusOnTaiwan) {
    Cesium.Camera.DEFAULT_VIEW_RECTANGLE = Cesium.Rectangle.fromDegrees(
      102,
      33,
      140,
      12,
    );
    Cesium.Camera.DEFAULT_VIEW_FACTOR = 1.5;
  }
  // CLOCK = new Cesium.Clock();

  if (GISPlatformToken) {
    Cesium.Ion.defaultAccessToken = GISPlatformToken;
  }

  const optionBaseLayerPicker =
    options["baseLayerPicker"] === undefined
      ? true
      : options["baseLayerPicker"];

  // const imageryViewModels = [];
  // imageryProviders.forEach(cur_option => {
  //   imageryViewModels.push(new Cesium.ProviderViewModel(cur_option));
  // });

  let viewer = new Cesium.Viewer(container, {
    ...options,
    // imageryProviderViewModels: imageryViewModels,
  });

  //Finally, create the baseLayerPicker viewer using our view models.
  const div = document.getElementsByClassName("cesium-viewer-toolbar")[0];
  div.setAttribute("id", "cesiumViewerToolbar");

  if (!optionBaseLayerPicker)
    document.getElementsByClassName(
      "cesium-baseLayerPicker-selected",
    )[0].parentNode.style.display = "none";

  if (options.skyBox === true) {
    viewer.scene.skyBox = createSkyBox(skyBoxAssetsBaseURL);
    viewer.scene.skyBox.show = true;
  } else if (options.skyBox === "default") {
    viewer.scene.skyBox.show = true;
  } else {
    viewer.scene.skyBox.show = false;
  }

  viewer.scene.skyAtmosphere.show = options.skyAtmosphere ?? true;
  viewer.scene.skyAtmosphere.brightnessShift = options.brightnessShift ?? 0.0;
  viewer.scene.globe.showGroundAtmosphere =
    options.showGroundAtmosphere ?? false;
  viewer.scene.globe.enableLighting = options.enableLighting ?? false;

  // load default infobox CSS
  if (options.infoBox) {
    loadStyle(viewer);
  }

  return viewer;
}
