export function _getType(object) {
  return Object.prototype.toString.call(object);
}

export function _isObject(object) {
  return _getType(object) === "[object Object]";
}

export function _isArray(object) {
  return _getType(object) === "[object Array]";
}

export function _isString(object) {
  return _getType(object) === "[object String]";
}

/**
 * Recursive parsing object and assigning raw's value.
 * @param object {Object} The raw data.
 */
export function recursiveParseObject(object, raw) {
  if (_isString(object)) {
    return object.startsWith("#CONST_")
      ? object.substring(7)
      : getNestedValue(raw, object);
  }

  const isObject = _isObject(object);
  const isArray = _isArray(object);
  if (isObject || isArray) {
    const res = isObject ? {} : [];
    for (const [key, value] of Object.entries(object)) {
      res[key] = recursiveParseObject(value, raw);
    }
    return res;
  } else {
    throw Error("Schema's name type must is String, Object or Array!");
  }
}

/**
 * A function for parsing given locational data, such as, a point of an AIS signal.
 * parsed data will be returned in the default form [id, coords, options],
 * where coords are [lng, lat]
 * @param raw {Object} The raw data.
 * @param schema {object} The fields you need.
 * @param parseOrder {array} Custom schema field names by order.
 */
export function parseFields(
  raw,
  schema,
  parseOrder = ["name", "coords", "options", "timestamp"],
) {
  const res = [];
  for (const field of parseOrder) {
    if (!Object.keys(schema).includes(field)) {
      console.warn(`${field} is not in schema.`);
    }
    res.push(recursiveParseObject(schema[field] || {}, raw));
  }
  return res;
}

/**
 * A function to get value from an object by a string key, nested structure is supported.
 * @param obj {Object} The raw data.
 * @param header {Object} The key to the data, must be string, nested value can be retrived by concating keys using "."
 */
export function getNestedValue(obj, key) {
  let value = {};
  Object.assign(value, obj);
  // Get nested value using splited by dot (".")
  key.split(".").forEach(k => {
    value = value[k];
  });
  return value;
}
