import * as Cesium from "cesium/Cesium";

/**
 *
 * [BETA]
 * Advanced camera control (will) provides a set of viewer.scene.camera's control, such as, rotation, focus, moveto and more.
 * @class AdvancedCameraControl
 */
class AdvancedCameraControl {
  constructor() {
    this.viewer = window.viewer;
    this.currentTimestamp = new Date();
    this.lastTimestamp = this.currentTimestamp;
    this.spinCallback = undefined;
    this.cameraPosition = null;
  }

  /**
   *
   * Start rotate the camera.
   * @param {number} [speed=1] speed of the rotation
   * @memberof AdvancedCameraControl
   */
  startSpinGlobe(speed = 1) {
    const viewer = window.viewer;
    this._updateTimestamp();
    this.spinCallback = () => {
      this._updateTimestamp();
      const timedelta =
        (this.currentTimestamp.valueOf() - this.lastTimestamp.valueOf()) / 7500;
      this.viewer.scene.camera.rotate(
        Cesium.Cartesian3.UNIT_Z,
        speed * timedelta,
      );
    };
    viewer.clock.onTick.addEventListener(this.spinCallback);
  }

  /**
   *
   * Stop rotate the camera.
   * @memberof AdvancedCameraControl
   */
  stopSpinGlobe() {
    this.viewer.clock.onTick.removeEventListener(this.spinCallback);
  }

  /**
   *
   * Get current camera position (Cartesian3).
   * @return {*}
   * @memberof AdvancedCameraControl
   */
  getCurrentCameraPosition() {
    return this.viewer.camera.position;
  }

  /**
   *
   * Set camera position by Cartesian3 (x, y, z).
   * @param {*} x
   * @param {*} y
   * @param {*} z
   * @memberof AdvancedCameraControl
   */
  setCameraPositionByCartesian3(x, y, z) {
    const position = new Cesium.Cartesian3(x, y, z);
    this.viewer.camera.position = position;
    this.cameraPosition = position;
  }

  /**
   *
   * Set camera position by coordinates (longitude, latitude, height).
   * @param {Number} longitude
   * @param {Number} latitude
   * @param {Number} height
   * @memberof AdvancedCameraControl
   */
  setCameraPositionByCoords(longitude, latitude, height) {
    const position = Cesium.Cartesian3.fromDegrees(longitude, latitude, height);
    this.viewer.camera.position = position;
    this.cameraPosition = position;
  }

  /**
   *
   * Set viewer.scene.globe.enableLighting.
   * @param {boolean} [v=false]
   * @memberof AdvancedCameraControl
   */
  setGlobeLighting(v = false) {
    if (v) {
      this.viewer.clock.shouldAnimate = true;
      this.viewer.clock.currenTime = Cesium.JulianDate.fromDate(new Date());
    } else {
      this.viewer.clock.shouldAnimate = false;
    }
    this.viewer.scene.globe.enableLighting = v;
  }

  /**
   *
   * Watch mouse pointer coords, and pass lon, lat to the given callback.<br>
   * It will pass coordinates in following format<br>
   * {<br>
   *  &emsp;coords: {<br>
   *  &emsp;&emsp;longitude: lon {Decimal Degrees}, <br>
   *  &emsp;&emsp;latitude: lat {Decimal Degrees}, <br>
   *  &emsp;}<br>
   * }<br>
   * Please note that the coords will be null if pointer location is invalid
   *
   * @param {Function} Event handler
   * @memberof AdvancedCameraControl
   */
  watchCurrentMouseCoords(callback) {
    this.viewer.scene.canvas.addEventListener("mousemove", e => {
      const ellipsoid = this.viewer.scene.globe.ellipsoid;
      const cartesian = this.viewer.camera.pickEllipsoid(
        new Cesium.Cartesian3(e.clientX, e.clientY),
        ellipsoid,
      );
      if (cartesian) {
        const cartographic = ellipsoid.cartesianToCartographic(cartesian);
        const lon = Cesium.Math.toDegrees(cartographic.longitude).toFixed(6);
        const lat = Cesium.Math.toDegrees(cartographic.latitude).toFixed(6);
        callback({
          coords: {
            longitude: lon,
            latitude: lat,
          },
        });
        return;
      }
      callback({
        coords: null,
      });
    });
  }

  /**
   *
   * Embed current camera location in URL, good for creating unified screen shots.
   * @memberof AdvancedCameraControl
   */
  embedCurrentFocusInURL() {
    this.url = new URL(location.href);
    const lastPos = this.url.searchParams.get("@");
    const mode = this.url.searchParams.get("m");
    if (mode) {
      this.viewer.scene.mode = parseInt(mode);
    }
    if (lastPos) {
      const [
        x,
        y,
        z,
        cameraDirectionX,
        cameraDirectionY,
        cameraDirectionZ,
        cameraUpX,
        cameraUpY,
        cameraUpZ,
      ] = lastPos.split(",");
      this.viewer.camera.position = new Cesium.Cartesian3(
        parseFloat(x),
        parseFloat(y),
        parseFloat(z),
      );
      this.viewer.camera.direction = new Cesium.Cartesian3(
        parseFloat(cameraDirectionX),
        parseFloat(cameraDirectionY),
        parseFloat(cameraDirectionZ),
      );
      this.viewer.camera.up = new Cesium.Cartesian3(
        parseFloat(cameraUpX),
        parseFloat(cameraUpY),
        parseFloat(cameraUpZ),
      );
    }
    this.viewer.camera.moveEnd.addEventListener(() => {
      this.cameraPosition = this.viewer.camera.position;
      this.cameraDirection = this.viewer.camera.direction;
      this.cameraUp = this.viewer.camera.up;
      this.sceneMode = this.viewer.scene.mode;
      this._updateURL();
    });
  }

  /**
   *
   * Remove current camera location from URL.
   * @memberof AdvancedCameraControl
   */
  clearCurrentFocusFromURL() {
    this.url.searchParams.delete("@");
    this.url.searchParams.delete("m");
    history.pushState(null, null, location.href);
  }

  _updateURL() {
    let focus = "";
    focus += `${this.cameraPosition.x},${this.cameraPosition.y},${this.cameraPosition.z},`;
    focus += `${this.cameraDirection.x},${this.cameraDirection.y},${this.cameraDirection.z},`;
    focus += `${this.cameraUp.x},${this.cameraUp.y},${this.cameraUp.z},`;
    this.url.searchParams.set("@", focus);
    this.url.searchParams.set("m", this.sceneMode);
    history.pushState(null, null, decodeURIComponent(this.url));
  }

  _updateTimestamp() {
    this.lastTimestamp = this.currentTimestamp;
    this.currentTimestamp = new Date();
  }

  destroy() {
    delete this.viewer;
    this.spinCallback = undefined;
    this.cameraPosition = null;
    this.clearCurrentFocusFromURL();
  }
}

export default AdvancedCameraControl;
