import { createCesiumViewer } from "./Cesium/viewer";
import {
  registerInteractCallback,
  registerDrillpickCallback,
  registerEntityClickCallback,
} from "./Cesium/picker";

import ScreenSpaceEventsManager from "./Cesium/ScreenSpaceEventsManager";

import _MapController from "./MapController";
import v from "./Utils/version";

var MapController = {};

/**
 *
 * Create viewer and initialize CoreMaps.MapController.
 * @param {string | Element} viewerContainerId The viewer container's id
 * @param {string} GISPlatformToken GIS Platform token
 * @param {string} imageryProviders GIS imagery providers
 * @param {Cesium.Viewer.ConstructorOptions} [options={}] [Cesium.Viewer.ConstructorOptions](https://cesium.com/learn/cesiumjs/ref-doc/Viewer.html#.ConstructorOptions)
 * @param {object} [dataControllerSettings=undefined] MapController settings, describes the dataflow.
 * @param {object} [coreMapsFeatures={}] Features that CoreMaps shipped to enable.
 */
function init(
  viewerContainerId,
  GISPlatformToken = null,
  imageryProviders = [],
  options = {},
  dataControllerSettings = undefined,
  coreMapsFeatures = {},
) {
  if (document.getElementById(viewerContainerId) == undefined)
    throw new Error(`${viewerContainerId} is not a valid DOM Object.`);
  if (dataControllerSettings == undefined)
    console.warn(
      `No MapController settings passed, only Cesium viewer will be rendered.`,
    );
  window.viewer = createCesiumViewer(
    viewerContainerId,
    options,
    GISPlatformToken,
    imageryProviders,
  );
  MapController = new _MapController(dataControllerSettings, coreMapsFeatures);
}

/**
 *
 * Destroy CoreMaps and Cesium viewer
 */
function destroy() {
  if (MapController !== {} || MapController !== undefined) {
    MapController.destroy();
    MapController = null;
  }
  window.viewer.entities.removeAll();
  window.viewer.destroy();
  window.viewer = null;
}

/**
 *
 * Get the CoreMaps version.
 * @return {string}
 */
function getVersion() {
  return v.version;
}

export {
  init,
  registerEntityClickCallback,
  destroy,
  MapController,
  getVersion,
  ScreenSpaceEventsManager,
};
