import html2canvas from "html2canvas";

import CMVersion from "@/CoreMaps/Utils/version";

/**
 *
 * ScreenShot service, provied an API to snapshot current viewer and view or download as a png/jpg image.
 * @class ScreenShot
 */
class ScreenShot {
  constructor() {
    this.viewer = window.viewer;
  }

  /**
   *
   * Capture current viewer and return an <img /> DOM Element
   * @return {Element}
   * @memberof ScreenShot
   */
  async capture() {
    this.viewer.render();
    const viewerCanvasDOM = this.viewer.canvas;

    const possibleContextType = ["webgl2", "webgl", "webgpu"];
    let viewerWebglContext = null;

    possibleContextType.forEach(contextType => {
      if (viewerWebglContext) {
        return;
      }
      viewerWebglContext = viewerCanvasDOM.getContext(contextType, {
        preserveDrawingBuffer: true,
      });
    });

    if (viewerWebglContext == undefined) {
      throw "Screenshot failed: Cannot viewerWebglContext";
    }

    const screenShotCanvasDOM = document.createElement("canvas");
    const screenShotCanvasContext = screenShotCanvasDOM.getContext("2d");

    const contextWidth = viewerCanvasDOM.clientWidth;
    const contextHeight = viewerCanvasDOM.clientHeight;

    screenShotCanvasDOM.width = contextWidth;
    screenShotCanvasDOM.height = contextHeight;

    screenShotCanvasContext.drawImage(viewerWebglContext.canvas, 0, 0);

    const scaleDOM = document.getElementById("scale-value").parentElement;
    if (scaleDOM) {
      const scaleCanvas = await html2canvas(scaleDOM, {
        backgroundColor: null,
      });

      const scaleLegendWidth = scaleDOM.clientWidth;
      const scaleLegendHeight = scaleDOM.clientHeight;

      screenShotCanvasContext.drawImage(
        scaleCanvas.getContext("2d").canvas,
        contextWidth - scaleLegendWidth * 2,
        contextHeight - scaleLegendHeight * 2,
        scaleLegendWidth,
        scaleLegendHeight,
      );
    }

    viewerWebglContext.preserveDrawingBuffer = false;

    const CMVersionText = `CM${CMVersion.version}`.toUpperCase();
    screenShotCanvasContext.font = "14px Courier New, Courier, monospace";
    screenShotCanvasContext.fillStyle = "#888888";
    screenShotCanvasContext.fillText(
      CMVersionText,
      contextWidth - 10 * CMVersionText.length,
      contextHeight - 12,
    );

    return screenShotCanvasDOM;
  }

  /**
   *
   * Capture current viewer and return as base64 string.
   * @return {string}
   * @memberof ScreenShot
   */
  async captureB64() {
    const screenShotCanvas = await this.capture();
    return screenShotCanvas.toDataURL();
  }

  /**
   *
   * Capture current viewer and return as blob.
   * @param {function} callback the callback to catch the blob
   * @return {blob}
   * @memberof ScreenShot
   */
  async captureBlob(callback) {
    const screenShotCanvas = await this.capture();
    return screenShotCanvas.toBlob(callback);
  }

  /**
   *
   * Capture current viewer and download as png.
   * @param {string} [fileNamePrefix=null] the filename prefix, a suffix "ScreenShot-ISODateString" will be appended.
   * @memberof ScreenShot
   */
  async captureAndDownload(fileNamePrefix = null) {
    const b64Image = await this.captureB64();
    const date = new Date();
    const linkDOM = document.createElement("a");
    linkDOM.setAttribute("href", b64Image);
    linkDOM.setAttribute(
      "download",
      `${
        fileNamePrefix ? fileNamePrefix : "CoreMaps"
      }-ScreenShot@${date.toISOString()}.png`,
    );
    linkDOM.click();
  }

  /**
   *
   * Capture current viewer and open in a new tab.
   * @param {string} [fileNamePrefix=null] the filename prefix, a suffix "ScreenShot-ISODateString" will be appended.
   * @memberof ScreenShot
   */
  captureAndOpenInNewTab(fileNamePrefix = null) {
    this.captureBlob(function(blob) {
      const imageDOM = new Image();
      const url = URL.createObjectURL(blob);

      imageDOM.src = url;
      const date = new Date();
      const fileName = `${
        fileNamePrefix ? fileNamePrefix : "CoreMaps"
      }-Screenshot@${date.toISOString()}.png`;

      const linkDOM = document.createElement("a");
      linkDOM.setAttribute("href", url);
      linkDOM.setAttribute("download", fileName);
      linkDOM.appendChild(imageDOM);

      const imageWindow = window.open("");
      imageWindow.document.write(
        `<head><title>${fileName}</title><style>body {background: #111 !important;color: #d0d0d0 !important;margin: 0;top: 0;left: 0;overflow: hidden;}h3{position: absolute;width: 100%;text-align: center;text-shadow: 1px 1px .3em #000;}</style></head>`,
      );
      imageWindow.document.write(`<center>`);
      imageWindow.document.write(`<h3>Click the image to download</h3>`);
      imageWindow.document.write(linkDOM.outerHTML);
      imageWindow.document.write(`</center>`);
    });
  }

  destroy() {
    delete this.viewer;
  }
}

export default ScreenShot;
