/**
 * DistanceMeasureTool (Measure line draw)
 * Refactored by Falcon
 *
 * Refs:
 *  - https://sandcastle.cesium.com/?src=Drawing%20on%20Terrain.html&label=All
 *  - https://www.programmersought.com/article/551089742/
 *
 */

/*eslint no-unused-vars: ["error", { "argsIgnorePattern": "^_" }]*/

import * as Cesium from "cesium/Cesium";

import { MapController } from "../../index";

/**
 *
 * Measure linear distance between points drawn on the map.
 * @class DistanceMeasureTool
 */
class DistanceMeasureTool {
  constructor() {
    this.viewer;
    this.activeShapePoints = [];
    this.activeDistance = [];
    this.activeShape;
    this.floatingPoint;

    this.measuring = false;
    this.viewerIsReady = false;

    this.handler;

    this.entities = [];
    this.labels = [];
    this.unit = "nm"; // "nm" || "km"
  }

  /**
   *
   * Initialize the instance, should be called before calling DistanceMeasureTool.start()
   * @memberof DistanceMeasureTool
   */
  init() {
    this.viewer = window.viewer;
    const viewer = this.viewer;
    if (!viewer.scene.pickPositionSupported) {
      window.alert("This browser does not support pickPosition.");
    }
    viewer.cesiumWidget.screenSpaceEventHandler.removeInputAction(
      Cesium.ScreenSpaceEventType.LEFT_DOUBLE_CLICK,
    );
    this.viewerIsReady = true;
  }

  /**
   *
   * Start the measurement.
   * @memberof DistanceMeasureTool
   */
  start() {
    if (!this.viewerIsReady) {
      this.init();
    }
    this.measuring = true;
    this.handler = new Cesium.ScreenSpaceEventHandler(this.viewer.canvas);
    MapController.shouldPickEntity = false;
    window.shouldPick = false;

    // Start draw
    this.leftClickEvent();
    document.getElementsByTagName("body")[0].style.cursor = "crosshair";

    // Drawing
    this.moveEvent(this, this.handler);

    // Stop draw
    this.rightClickEvent(this, this.handler);
  }

  getLineDis(startPoint, endPoint) {
    const startCartographicPoint = Cesium.Cartographic.fromCartesian(
      startPoint,
    );
    const endCartographicPoint = Cesium.Cartographic.fromCartesian(endPoint);
    const ellipsoidGeodesic = new Cesium.EllipsoidGeodesic(
      startCartographicPoint,
      endCartographicPoint,
    );
    return ellipsoidGeodesic.surfaceDistance;
  }

  sumDis(arr) {
    const sum = arr.reduce((curr, prev) => {
      curr += prev;
      return curr;
    }, 0);
    return sum;
  }

  createPoint(worldPosition, distance = null) {
    const viewer = window.viewer;
    const pointOption = {
      name: "Measure Point",
      position: worldPosition,
      point: {
        outlineColor: Cesium.Color.WHITE,
        outlineWidth: 1,
        pixelSize: 10,
        color: new Cesium.Color(1.0, 99 / 255, 71 / 255, 0.5),
        heightReference: Cesium.HeightReference.CLAMP_TO_GROUND,
      },
    };
    if (distance) {
      pointOption["label"] = {
        // This callback updates the length to print each frame.
        text:
          this.unit == "nm"
            ? `${(distance / 1.852).toFixed(3)} nm`
            : `${distance.toFixed(3)} km`,
        font: "12px sans-serif",
        pixelOffset: new Cesium.Cartesian2(0.0, 20),
      };
    }
    const point = viewer.entities.add(pointOption);
    return point;
  }

  createMidLabel(position, distance = null) {
    const viewer = window.viewer;
    const pointOption = {
      name: "Measure Point",
      position: position,
      label: {
        // This callback updates the length to print each frame.
        text:
          this.unit == "nm"
            ? `${(distance / 1.852).toFixed(3)} nm`
            : `${distance.toFixed(3)} km`,
        font: "12px sans-serif",
        pixelOffset: new Cesium.Cartesian2(0.0, 20),
      },
    };
    const label = viewer.entities.add(pointOption);
    return label;
  }

  drawShape(positionData) {
    const viewer = window.viewer;
    const shape = viewer.entities.add({
      name: "drawPolyLine",
      polyline: {
        positions: positionData,
        material: new Cesium.PolylineArrowMaterialProperty(
          new Cesium.Color(1.0, 99 / 255, 71 / 255, 1),
        ),
        // clampToGround: true,
        width: 10,
      },
    });
    return shape;
  }

  leftClickEvent() {
    //The left mouse button click event
    this.handler.setInputAction(event => {
      if (!this.measuring) {
        return;
      }
      let ray = this.viewer.camera.getPickRay(event.position); // movement using position
      const earthPosition = this.viewer.scene.globe.pick(
        ray,
        this.viewer.scene,
      );
      if (!earthPosition) {
        return;
      }

      if (Cesium.defined(earthPosition)) {
        if (this.activeShapePoints.length === 0) {
          this.floatingPoint = this.createPoint(earthPosition);
          this.activeShapePoints.push(earthPosition);
          const dynamicPositions = new Cesium.CallbackProperty(() => {
            return this.activeShapePoints;
          }, false);
          this.activeShape = this.drawShape(dynamicPositions);
        }
        this.activeShapePoints.push(earthPosition);
        if (this.activeShapePoints.length > 2) {
          const startPoint = this.activeShapePoints[
            this.activeShapePoints.length - 2
          ];
          const endPoint = this.activeShapePoints[
            this.activeShapePoints.length - 3
          ];
          const _distance = this.getLineDis(startPoint, endPoint);
          const midPoint = Cesium.Cartesian3.midpoint(
            startPoint,
            endPoint,
            new Cesium.Cartesian3(),
          );
          this.activeDistance.push(_distance);
          const displayDistance = Math.round(Math.round(_distance) / 10) / 100;
          const label = this.createMidLabel(midPoint, displayDistance);
          this.entities.push(label);
          this.labels.push({
            label: label,
            distanceKM: displayDistance,
          });
        }
        const sumDistance =
          Math.round(Math.round(this.sumDis(this.activeDistance)) / 10) / 100;
        const point = this.createPoint(earthPosition, sumDistance);
        this.labels.push({
          label: point,
          distanceKM: sumDistance,
        });
        this.entities.push(point);
        this.viewer.selectedEntity = null;
        this.viewer.trackedEntity = null;
      }
    }, Cesium.ScreenSpaceEventType.LEFT_CLICK);
  }

  moveEvent(lineMeasure, handler) {
    //The movement event
    handler.setInputAction(event => {
      if (Cesium.defined(lineMeasure.floatingPoint)) {
        const ray = lineMeasure.viewer.camera.getPickRay(event.endPosition); // movement using endPosition
        const newPosition = lineMeasure.viewer.scene.globe.pick(
          ray,
          lineMeasure.viewer.scene,
        );

        if (Cesium.defined(newPosition)) {
          lineMeasure.floatingPoint.position.setValue(newPosition);
          lineMeasure.activeShapePoints.pop();
          lineMeasure.activeShapePoints.push(newPosition);
        }
      }
    }, Cesium.ScreenSpaceEventType.MOUSE_MOVE);
  }

  rightClickEvent(lineMeasure, handler) {
    //The right mouse button click event
    handler.setInputAction(_event => {
      document.getElementsByTagName("body")[0].removeAttribute("style");
      lineMeasure.measuring = false;
      lineMeasure.activeShapePoints.pop();
      const lineShape = this.drawShape(lineMeasure.activeShapePoints);
      lineMeasure.entities.push(lineShape);
      if (handler) {
        handler.destroy();
        lineMeasure.handler = null;
      }
      if (lineMeasure.floatingPoint) {
        lineMeasure.viewer.entities.remove(lineMeasure.floatingPoint);
      }
      if (lineMeasure.activeShape) {
        lineMeasure.viewer.entities.remove(lineMeasure.activeShape);
      }

      lineMeasure.floatingPoint = undefined;
      lineMeasure.activeShape = undefined;
      lineMeasure.activeShapePoints = [];
      lineMeasure.activeDistance = [];
      MapController.shouldPickEntity = true;
      window.shouldPick = true;
    }, Cesium.ScreenSpaceEventType.RIGHT_CLICK);
  }

  /**
   *
   *
   * @param {"nm" | "km"} [unit="nm"]
   * @memberof DistanceMeasureTool
   */
  switchUnit(unit = "nm") {
    this.unit = unit;
    this.labels.forEach(label => {
      if (label.label.label) {
        label.label.label.text =
          this.unit == "nm"
            ? `${(label.distanceKM / 1.852).toFixed(3)} nm`
            : `${label.distanceKM.toFixed(3)} km`;
      }
    });
  }

  /**
   *
   * Clear all measurement points.
   * @memberof DistanceMeasureTool
   */
  clear() {
    this.entities.forEach(entity => {
      this.viewer.entities.remove(entity);
    });
    this.entities = [];
    this.labels = [];
  }

  /**
   *
   * Destroy instance and free memory
   * @memberof DistanceMeasureTool
   */
  destroy() {
    for (const key of Object.getOwnPropertyNames(this)) {
      this[key] = null;
    }
  }
}

export default DistanceMeasureTool;
