import Polling from "./Polling";
import { parseFields } from "../Utils/helper";

/**
 * DataController is a fundamental data controller
 * @param spatials {object} An JOSN-like object describes where and how the data should be fetched and how it should be paresd and visualized.
 */
class DataController {
  constructor(spatials) {
    this.spatials = {};
    this.schemata = {};
    this.pollings = {};
    this.apis = {};

    if (Object.keys(spatials).length == 0) {
      throw Error(
        `${this.constructor.name} must provide at least one spatial.`,
      );
    }
    Object.keys(spatials).forEach(key => {
      const spatial = spatials[key];
      this.spatials[key] = new spatial.spatial(spatial.entityType);
      this.schemata[key] = spatial.schema;
      this.apis[key] = spatial.api;
      if (spatial.isPolling) {
        this.pollings[key] = this.createPolling(null, 5000, key);
      }
    });
  }

  /**
   * Fetch an item (entity / primitive)'s data by its dedicated API and schema and render them.\
   * **Please note that only array type responses are supported now**.
   * @param spatial {string} The spatial type.
   * @param apiParameters {Array} The parameters for the api method call.
   * @param callback {Function} (optional) A callback function called when the response is given.
   */
  async fetchAndAdd(spatial, apiParameters = [], callback = undefined) {
    const api = this.apis[spatial];
    const schema = this.schemata[spatial];
    if (api === undefined) {
      throw new Error(`No available api for ${spatial}.`);
    }
    if (schema === undefined) {
      throw new Error(`No schema defined for ${spatial}.`);
    }

    // api should return a json object in the future.
    const objects = await api(...apiParameters);

    // Support for fetch API, which will be deprecated soon.
    // const objects = await response.json();
    // Support for axios, which will be deprecated soon.
    // const objects = response.data;

    this.clear(spatial);
    let updated = Array();
    objects.forEach(object => {
      let [id, coords, options, timestamp] = parseFields(object, schema);
      this.spatials[spatial].add(id, [...coords, timestamp ?? 0], options);
      updated.push(id);
    });
    if (callback) callback(updated);
  }

  /**
   * Update an item (entity / primitive) to a spatial.
   * @param id {string} An unique id for the entity.
   * @param point {Array} Coordinate(s) of the item and time in form [lnt, lat, time] or [[lnt, lat, time], ...].
   * @param options {Object} Extra info for the entity.
   * @param spatial {string} The spatial type.
   */
  add(id, point, options = {}, spatial = "point") {
    this.spatials[spatial].add(id, point, options);
  }

  /**
   * Update an item (entity / primitive) from a spatial by id.
   * @param id {string} An unique id for the entity.
   * @param point {Array} Coordinate(s) of the item and time in form [lnt, lat, time] or [[lnt, lat, time], ...].
   * @param options {Object} Extra info for the entity.
   * @param spatial {String} The spatial type.
   */
  update(id, point, options = {}, spatial = "point") {
    this.spatials[spatial].update(id, point, options);
  }

  /**
   * Remove an item (entity / primitive) from a spatial by id.
   * @param id {string} (Optional) The item's id.
   * @param spatial {string} (Optional) The spatial type.
   */
  remove(id, spatial = "point") {
    this.spatials[spatial].remove(id);
  }

  /**
   * Clear spatial(s), If spatial name is given, the specified spatial will be cleared. Or, all spatial will be cleared.
   * @param spatial {string} (Optional) The spatial type.
   */
  removeAll(spatial = "point") {
    this.spatials[spatial].removeAll();
  }

  /**
   * Clear spatial(s), If spatial name is given, the specified spatial will be cleared. Or, all spatial will be cleared.
   * @param spatial {string} (Optional) The spatial type.
   */
  clear(spatial = undefined) {
    if (spatial) {
      this.spatials[spatial].removeAll();
    } else {
      Object.keys(this.spatials).forEach(spatial => {
        this.spatials[spatial].removeAll();
      });
    }
  }

  /**
   * Filter spatial(s)
   * @param spatial {string} (Optional) if given, the specified spatial will be cleared. Otherwise, all spatial will be cleared.
   */
  filter(cmp, setVisibility = undefined, mode = undefined, spatial = "point") {
    this.spatials[spatial].filter(cmp, setVisibility, mode);
  }

  /**
   * Create a polling
   * @abstract createPolling() Must be implemented if the vessel's datasource is real-time capable and stream-like features are needed.
   */
  async createPolling(fn = undefined, interval = 5000, spatial = "point") {
    console.warn(
      `Method createPolling does not implement by "${this.constructor.name}", unexpected behavior may occur.`,
    );
    this.pollings[spatial] = new Polling(
      this.apis[spatial],
      this.spatials[spatial],
      this.schemata[spatial],
      null,
      interval,
      fn,
    );
  }

  /**
   * Start a polling
   * @param polling {string} The polling's name.
   */
  async startPolling(polling) {
    await this.pollings[polling].start();
  }

  /**
   * Stop a polling, if polling name is given, the specified polling will be stopped. Otherwise, all polling will be stopped, and all spatials will be stopped.
   * @param polling {string} (Optional)
   */
  async stopPolling(polling = undefined) {
    if (!polling) {
      this.destroy();
      return;
    }
    if (!Object.keys(this.pollings).indexOf(polling) == -1) {
      console.warn(
        `Polling ${polling} does not exist, so it won't be stopped.`,
      );
      return;
    }
    if (this.pollings[polling].isPolling === true)
      this.pollings[polling].stop();
    this.clear(polling);
  }

  /**
   * Clear all spatials, stop all pollings, free all memory used within this DataController
   */
  destroy() {
    Object.keys(this.pollings).forEach(k => {
      this.stopPolling(k);
    });
  }

  getIDs(spatial = "point") {
    return this.spatials[spatial].getIDs();
  }

  getSpatialRawData(id, spatial = "point") {
    return this.spatials[spatial].getRaw(id);
  }
}

export default DataController;
