/*eslint-disable no-unused-vars */
import * as Cesium from "cesium/Cesium";
import * as turf from "@turf/turf";

/**
 *
 * Export spatials within a free drawn polygon or circle.
 * @todo Primitives exporting has not supported yet.
 * @class AreaExporter
 */
class AreaExporter {
  constructor(MapController) {
    this.init(MapController);
  }

  init(MapController) {
    this.mode = "polygon";
    this.viewer = window.viewer;
    this.activeShapePoints = [];
    this.activeShape = undefined;
    this.floatingPoint = undefined;
    this.isDrawing = false;
    this.handler = undefined;
    this.entities = [];
    this.unit = "meter";
    this.radius = 0;
    this.polygonCoords = [];
    this.centre = [];
    this.MapController = MapController;
  }

  /**
   *
   *
   * @param {*} [callback=undefined]
   * @memberof AreaExporter
   */
  drawPolygon(callback = undefined) {
    if (!window.viewer.scene.pickPositionSupported) {
      throw new Error("This browser does not support pickPosition.");
    }
    window.shouldPick = false;
    // window.viewer.scene.globe.depthTestAgainstTerrain = true;
    this.handler = new Cesium.ScreenSpaceEventHandler(this.viewer.canvas);
    this.mode = "polygon";

    this.viewer.cesiumWidget.screenSpaceEventHandler.removeInputAction(
      Cesium.ScreenSpaceEventType.LEFT_DOUBLE_CLICK,
    );
    document.getElementsByTagName("body")[0].style.cursor = "crosshair";
    this._registerPolygonLeftClickEvent();
    this._registerPolygonMouseMoveEvent();
    this._registerPolygonRightClickEvent(callback);
  }

  /**
   * @param {"meters"|"miles"} [unit="meters"]
   * @param {number} [radius=500000]
   * @param {*} [callback=undefined]
   * @memberof AreaExporter
   */
  drawCircle(unit = "meters", radius = 500000, callback = undefined) {
    if (!window.viewer.scene.pickPositionSupported) {
      throw new Error("This browser does not support pickPosition.");
    }
    window.shouldPick = false;
    this.handler = new Cesium.ScreenSpaceEventHandler(window.viewer.canvas);
    this.mode = "circle";
    this.unit = unit;
    if (unit == "miles") {
      this.radius = Math.floor(radius * 1852);
      this.label = `${radius} NM`;
    } else {
      this.radius = Math.floor(radius);
      this.label = `${radius} Meter${radius == 1 ? "" : "s"}`;
    }
    document.getElementsByTagName("body")[0].style.cursor = "crosshair";

    this._registerCircleMouseMoveClickEvent();
    this._registerCircleLeftClickEvent(callback);
  }

  /**
   *
   * @private
   * @param {*} position
   * @param {*} [label=undefined]
   * @return {*}
   * @memberof AreaExporter
   */
  createPoint(position, label = undefined) {
    let pointOptions = {
      position: position,
      point: {
        color: Cesium.Color.WHITE,
        pixelSize: 5,
        heightReference: Cesium.HeightReference.CLAMP_TO_GROUND,
      },
    };
    if (label) {
      pointOptions["label"] = {
        text: label,
        font: "15px sans-serif",
        pixelOffset: new Cesium.Cartesian2(0.0, 20),
      };
    }
    return window.viewer.entities.add(pointOptions);
  }

  /**
   *
   * @private
   * @param {*} position
   * @return {*}
   * @memberof AreaExporter
   */
  _drawPolygon(position) {
    return window.viewer.entities.add({
      polygon: {
        hierarchy: position,
        material: new Cesium.ColorMaterialProperty(
          Cesium.Color.YELLOWGREEN.withAlpha(0.5),
        ),
      },
    });
  }

  /**
   *
   * @private
   * @param {*} position
   * @param {*} radius
   * @return {*}
   * @memberof AreaExporter
   */
  _drawEllipse(position, radius) {
    return window.viewer.entities.add({
      position: position,
      ellipse: {
        semiMinorAxis: radius,
        semiMajorAxis: radius,
        material: Cesium.Color.PALEVIOLETRED.withAlpha(0.3),
      },
    });
  }

  /**
   *
   * @private
   * @memberof AreaExporter
   */
  _registerPolygonLeftClickEvent() {
    this.handler.setInputAction(event => {
      const earthPosition = this.viewer.camera.pickEllipsoid(event.position);
      if (Cesium.defined(earthPosition)) {
        if (this.activeShapePoints.length === 0) {
          this.floatingPoint = this.createPoint(earthPosition);
          this.activeShapePoints.push(earthPosition);
          const dynamicPosition = new Cesium.CallbackProperty(() => {
            return new Cesium.PolygonHierarchy(this.activeShapePoints);
          }, false);
          this.activeShape = this._drawPolygon(dynamicPosition);
        }
        this.isDrawing = true;
        this.activeShapePoints.push(earthPosition);
        this.entities.push(this.createPoint(earthPosition));
        this.viewer.selectedEntity = null;
        const coord = Cesium.Cartographic.fromCartesian(earthPosition);
        this.polygonCoords.push([
          parseFloat(Cesium.Math.toDegrees(coord["latitude"])),
          parseFloat(Cesium.Math.toDegrees(coord["longitude"])),
        ]);
      }
    }, Cesium.ScreenSpaceEventType.LEFT_CLICK);
  }

  /**
   *
   * @private
   * @memberof AreaExporter
   */
  _registerPolygonMouseMoveEvent() {
    this.handler.setInputAction(event => {
      if (!this.isDrawing) return;
      const lastMovedPosition = this.viewer.camera.pickEllipsoid(
        event.endPosition,
      );
      if (Cesium.defined(lastMovedPosition)) {
        this.floatingPoint.position.setValue(lastMovedPosition);
        this.activeShapePoints.pop();
        this.activeShapePoints.push(lastMovedPosition);
      }
    }, Cesium.ScreenSpaceEventType.MOUSE_MOVE);
  }

  /**
   *
   * @private
   * @param {*} [callback=undefined]
   * @memberof AreaExporter
   */
  _registerPolygonRightClickEvent(callback = undefined) {
    this.handler.setInputAction(_ => {
      this.activeShapePoints.pop();
      const polygon = this._drawPolygon(this.activeShapePoints);
      this.entities.push(polygon);

      this.viewer.entities.remove(this.floatingPoint);
      this.viewer.entities.remove(this.activeShape);

      this.floatingPoint = undefined;
      this.activeShape = undefined;
      this.activeShapePoints = [];
      this.isDrawing = false;
      this.viewer.selectedEntity = null;
      this.handler.destroy();

      document.getElementsByTagName("body")[0].removeAttribute("style");
      const result = this.calculatePiPResult(coords => {
        return this.isInPolygon(coords);
      });
      if (callback) callback(result);
      window.shouldPick = true;
      return result;
    }, Cesium.ScreenSpaceEventType.RIGHT_CLICK);
  }

  /**
   *
   * @private
   * @memberof AreaExporter
   */
  _registerCircleMouseMoveClickEvent() {
    this.handler.setInputAction(event => {
      const lastMovedPosition = this.viewer.camera.pickEllipsoid(
        event.endPosition,
      );
      if (Cesium.defined(lastMovedPosition)) {
        if (this.floatingPoint === undefined && this.activeShape == undefined) {
          const center = this.createPoint(lastMovedPosition, this.label);
          const circle = this._drawEllipse(lastMovedPosition, this.radius);
          this.floatingPoint = center;
          this.activeShape = circle;
        } else {
          this.floatingPoint.position.setValue(lastMovedPosition);
          this.activeShape.position.setValue(lastMovedPosition);
        }
      }
    }, Cesium.ScreenSpaceEventType.MOUSE_MOVE);
  }

  /**
   *
   * @private
   * @param {*} [callback=undefined]
   * @memberof AreaExporter
   */
  _registerCircleLeftClickEvent(callback = undefined) {
    this.handler.setInputAction(event => {
      const earthPosition = this.viewer.camera.pickEllipsoid(event.position);
      this.centre = Cesium.Cartographic.fromCartesian(earthPosition);
      const center = this.createPoint(earthPosition, this.label);
      const circle = this._drawEllipse(earthPosition, this.radius);
      this.entities.push(center);
      this.entities.push(circle);
      this.viewer.entities.remove(this.floatingPoint);
      this.viewer.entities.remove(this.activeShape);
      this.floatingPoint = null;
      this.activeShape = null;
      this.viewer.selectedEntity = null;
      this.label = null;
      this.handler.destroy();
      document.getElementsByTagName("body")[0].removeAttribute("style");

      const result = this.calculatePiPResult(coords => {
        return this.isInCircle(coords);
      });
      if (callback) callback(result);
      window.shouldPick = false;
      return result;
    }, Cesium.ScreenSpaceEventType.LEFT_CLICK);
  }

  calculatePiPResult(interpreter = undefined) {
    if (interpreter === undefined)
      throw new Error("Interpreter should be a valid function");
    const entities = this.MapController.getAllEntities();
    let result = [];
    entities.forEach(entity => {
      if (interpreter(entity)) {
        result.push(entity);
      }
    });
    return result;
  }

  /**
   *
   * @param {Array} [polygonCoords=[]] A list of geoJSON coords
   * @return {Array} Returns array of entities within given polygon
   * @memberof AreaExporter
   */
  calculatePiPResultFromCustomGeoJSONPolygon(polygonCoords = []) {
    if (polygonCoords.length === 0) throw new Error("No coords passed");
    let _polygonCoords = [];
    polygonCoords.forEach(coords => {
      _polygonCoords.push([coords[1], coords[0]]);
    });
    const polygon = turf.polygon([_polygonCoords]);
    const entities = this.MapController.getAllEntities();
    let result = [];
    entities.forEach(entity => {
      if (entity.position) {
        const coords = Cesium.Cartographic.fromCartesian(
          entity.position._value,
        );
        const lat = parseFloat(Cesium.Math.toDegrees(coords.latitude));
        const lng = parseFloat(Cesium.Math.toDegrees(coords.longitude));
        const targetPoint = turf.point([lat, lng]);
        if (turf.booleanPointInPolygon(targetPoint, polygon)) {
          result.push(entity);
        }
      }
    });
    return result;
  }

  /**
   *
   * Originally called "booleanInPolygon"
   * @private
   * @param {*} entity
   * @return {*}
   * @memberof AreaExporter
   */
  isInPolygon(entity) {
    const coords = Cesium.Cartographic.fromCartesian(entity.position._value);
    const lat = parseFloat(Cesium.Math.toDegrees(coords.latitude));
    const lng = parseFloat(Cesium.Math.toDegrees(coords.longitude));

    let targetPoint = turf.point([lat, lng]);
    const polygonCoords = this.polygonCoords.slice();
    polygonCoords.push(this.polygonCoords[0]);
    let polygon = turf.polygon([polygonCoords]);
    return turf.booleanPointInPolygon(targetPoint, polygon);
  }

  /**
   *
   * Originally called "booleanInCircle"
   * @private
   * @param {*} entity
   * @return {*}
   * @memberof AreaExporter
   */
  isInCircle(entity) {
    const targetPointCoord = Cesium.Cartographic.fromCartesian(
      entity.position._value,
    );
    const ellipsoidGeodesic = new Cesium.EllipsoidGeodesic(
      targetPointCoord,
      this.centre,
    );
    return ellipsoidGeodesic.surfaceDistance <= this.radius;
  }

  /**
   *
   * Clear all existing exporter polygons.
   * @memberof AreaExporter
   */
  clear() {
    this.entities.forEach(entity => {
      window.viewer.entities.remove(entity);
    });
    window.viewer.scene.globe.depthTestAgainstTerrain = false;
    this.entities = [];
    this.init(this.MapController);
  }

  /**
   *
   * Destroy instance and free memory
   * @memberof AreaExporter
   */
  destroy() {
    this.clear();
    this.polygonCoords = [];
    this.centre = [];
  }
}
export default AreaExporter;
