import Dataflow from "@/CoreMaps/Dataflow";

class ExampleDataController extends Dataflow.DataController {
  constructor(spatials) {
    super(spatials);
  }

  async addMultipleData(dataList, spatial = "point") {
    this.spatials.point.performancedBatchRender(dataList);
    const cut = 5000;
    for (let i = 0; i < Math.ceil(dataList.length / cut); i++) {
      const firstIndex = i * cut;
      const lastIndex = i * cut + cut;
      const slicedData = dataList.slice(firstIndex, lastIndex)
      setTimeout(() => {
        slicedData.forEach(data => {
          this.spatials[spatial].add(data.id, data.coords);
        });
      }, 200);
    }
    dataList.forEach(data => {
      this.spatials[spatial].add(data.id, data.coords);
    });
  }

  renderRoute(data) {
    this.spatials.route.add(data.name, data.coords);
  }

  renderGrid(data) {
    this.spatials.grid.add(data.name, data.coords);
  }

}

export default ExampleDataController;
