import * as Cesium from "cesium/Cesium";

/**
 *
 * A GeoJSON datasource manager, will load geojson only when needed, loaded layers will be cached for later use.
 * @class GeoJSONLayer
 */
class GeoJSONLayer {
  constructor() {
    this.viewer = window.viewer;
    this.geoJsons = {};
  }

  /**
   *
   * Toggle layer's visibility
   * @param {Boolean} visibility
   * @param {String} layerName
   * @param {Object} defaultPointIcon Default point icon describer in following format
   * @param {Boolean} foucs set true to fly to the polygons after toggling on a layer
   * @example {
   *  icon: "String: the icon or icon path"
   *  height: "Number(optional, default is 24): icon height"
   *  width: "Number(optional, default is 24): icon width"
   * }
   *
   */
  async toggleLayer(
    visibility,
    layerName,
    defaultPointIcon = undefined,
    focus = false,
  ) {
    const _layer = this.geoJsons[layerName];
    if (_layer === undefined) throw new Error(`Layer ${layerName} not found.`);
    if (visibility && !_layer.rendered) {
      const data = await _layer.api();
      this.loadGeoJSON(layerName, data, defaultPointIcon);
      this.geoJsons[layerName].rendered = true;
    } else {
      this.geoJsons[layerName].dataSource.show = visibility;
    }
    if (focus && visibility) {
      this.viewer.flyTo(this.geoJsons[layerName].dataSource.entities, {
        offset: new Cesium.HeadingPitchRange(0, -Math.PI / 2),
      });
    }
  }

  /**
   *
   * Register a new GeoJSON layer.
   * @param {object} config key represents the name of the layer, each paired with a function that will return a valid GeoJSON feature.
   * @example
   * registerLayer({
   *   eez: {
   *     api: async function() { return polygonAPI.getPolygonsByType("eez"); }
   *     arcType: "RHUMB" (default) | "GEODESIC",
   *     fillColor: "#ffffff15",
   *     strokeColor: "#ffffff40",
   *   }
   * });
   */
  registerLayer(config = { api: "" }) {
    Object.keys(config).forEach(name => {
      const api = config[name].api;
      if (this.geoJsons[name]) {
        console.warn(
          `A layer named ${name} is already exists, the new layer will not be created.`,
        );
        return false;
      }
      if (!api) {
        throw new Error(
          `An API must be provided. The new layer will not be created.`,
        );
      }
      this.geoJsons[name] = {
        api: api,
        rendered: false,
        dataSource: new Cesium.GeoJsonDataSource(name),
        properties: {},
        arcType: config[name].arcType || "RHUMB",
        fillColor: config[name].fillColor || "#ffffff15",
        strokeColor: config[name].strokeColor || "#ffffff40",
      };
      return true;
    });
  }

  /**
   *
   * Remove/unregister an existing GeoJSON layer.
   * @param {string} layer The layer to remove / unregister
   */
  unregisterLayer(layer) {
    if (Object.keys(this.geoJsons).includes(layer)) {
      if (this.geoJsons[layer].dataSource) {
        this.viewer.dataSources.remove(this.geoJsons[layer].dataSource);
      }
      delete this.geoJsons[layer];
    } else {
      console.warn(
        `Trying to unregister non-existing GeoJSON layer "${layer}"`,
      );
    }
  }

  /**
   *
   * @private
   * Create layer
   * @param {String} name
   * @return {Boolean}
   */
  createLayer(name) {
    if (this.geoJsons[name]) {
      console.warn(
        `A layer named ${name} is already exists, the new layer will not be created.`,
      );
      return false;
    }
    this.geoJsons[name] = {
      api: undefined,
      rendered: false,
      dataSources: {},
      properties: {},
    };
    return true;
  }

  /**
   *
   * @private
   * check if layer is exists.
   * @param {String} name
   * @return {Boolean}
   */
  layerExists(name) {
    return this.geoJsons[name];
  }

  /**
   *
   * @private
   * Load layer for the first time.
   * @param {String} layer the layer's name
   * @param {Array} geoJSON array of geoJSONs
   * @param {Object} pointIcon a point icon describer
   *
   */
  async loadGeoJSON(layer, geoJSON, pointIcon = undefined) {
    // if (this.geoJsons[layer] === undefined) {
    //   this.createLayer(layer);
    // }
    const geoJsonFeatures = {
      type: "FeatureCollection",
      features: geoJSON,
    };

    const layerConfig = this.geoJsons[layer];

    await layerConfig.dataSource.load(geoJsonFeatures, {
      fill: Cesium.Color.fromCssColorString(layerConfig.fillColor),
      strokeWidth: 0.5,
      stroke: Cesium.Color.fromCssColorString(layerConfig.strokeColor),
      markerColor: Cesium.Color.fromCssColorString("#00000000"),
      markerSize: 0,
    });
    await this.viewer.dataSources.add(layerConfig.dataSource);

    // Prevent polygon from broken
    this.geoJsons[layer].dataSource.entities.values.forEach(entity => {
      entity.getSpatialType = () => {
        return layer;
      };

      entity.label = {
        text: entity.name,
        font: "12.5pt Helvetica",
        backgroundColor: Cesium.Color.fromCssColorString("#00000070"),
        showBackground: true,
        verticalOrigin: Cesium.VerticalOrigin.BOTTOM,
        pixelOffset: new Cesium.Cartesian2(0, -15),
        pixelOffsetScaleByDistance: new Cesium.NearFarScalar(
          250,
          1.0,
          15000000,
          0.4,
        ),
        scaleByDistance: new Cesium.NearFarScalar(250, 1.0, 15000000, 0.4),
      };

      if (!entity.billboard) {
        // layer is polygon / multipolygon
        entity.polygon.arcType = Cesium.ArcType[layerConfig.arcType];
        const polyPositions = entity.polygon.hierarchy.getValue(
          Cesium.JulianDate.now(),
        ).positions;
        const polyCenter = Cesium.BoundingSphere.fromPoints(polyPositions)
          .center;
        entity.position = Cesium.Ellipsoid.WGS84.scaleToGeodeticSurface(
          polyCenter,
        );
      } else {
        // layer is not polygon
        entity.billboard = undefined;
        if (entity.properties.icon || pointIcon) {
          const icon = (entity.properties.icon || {})._value || pointIcon;
          entity.billboard = {
            image: icon.icon,
            height: icon.height || 24,
            width: icon.width || 24,
            scaleByDistance: new Cesium.NearFarScalar(250, 1.0, 15000000, 0.25),
          };
        } else {
          entity.point = {
            Color: Cesium.Color.fromCssColorString(layerConfig.fillColor),
            pixelSize: 10,
            scaleByDistance: new Cesium.NearFarScalar(250, 1.0, 15000000, 0.25),
          };
        }
      }
    });
  }

  /**
   *
   * Toggle layer's label
   * @param {String} layer The layer.
   * @param {Boolean} visibility The visibility.
   * @memberof GeoJSONLayer
   */
  toggleLabel(layer, visibility) {
    this.geoJsons[layer].dataSource.entities.values.forEach(entity => {
      entity.label.show = visibility;
    });
  }

  /**
   *
   * Remove all existing GeoJSONLayers
   * @memberof GeoJSONLayer
   */
  clear() {
    Object.keys(this.geoJsons).forEach(k => {
      this.unregisterLayer(k);
    });
  }

  /**
   *
   * Get a entity's info by given layer and entity id.
   * @param {*} layer
   * @param {*} id
   * @return {*}
   */
  getInfo(layer, id) {
    const entity = this.geoJsons[layer].dataSource.entities.getById(id);
    if (entity) {
      const propertyNames = entity.properties.propertyNames;
      return propertyNames.reduce((acc, cur) => {
        acc[cur] = entity.properties[cur];
        return acc;
      }, {});
    }
  }

  destroy() {
    this.clear();
  }
}

export default GeoJSONLayer;
