import {
  AdvancedCameraControl,
  DistanceMeasureTool,
  PolygonDrawer,
  AreaExporter,
  ScreenShot,
  Graticule,
  Scale,
} from "./Cesium/MeasureKit";

import {
  selectOnMap,
  selectEntity,
  selectEntityById,
  selectEntityFromDataSourceById,
  getEntityById,
  getRawDataById,
  getAllEntities,
} from "./Cesium/picker";

import { BaseCesiumSpatial } from "./Cesium";

import { GeoJSONLayer } from "./Cesium/Layers";
class MapController {
  _defaultFeatures = {
    DistanceMeasureTool: false,
    PolygonDrawer: false,
    AreaExporter: true,
    Graticule: false,
    GeoJSONLayer: false,
    Scale: false,
    ScreenShot: false,
    CameraControl: false,
    EmbedCurrentFocusInURL: false,
  };
  _features = {
    DistanceMeasureTool: () => {
      return new DistanceMeasureTool();
    },
    PolygonDrawer: () => {
      return new PolygonDrawer();
    },
    AreaExporter: () => {
      return new AreaExporter(this);
    },
    Graticule: () => {
      return new Graticule({
        tileWidth: 512,
        tileHeight: 512,
        numLines: 10,
      });
    },
    GeoJSONLayer: geoJSONLayerConfig => {
      const geoJSONLayer = new GeoJSONLayer();
      geoJSONLayer.registerLayer(geoJSONLayerConfig);
      return geoJSONLayer;
    },
    Scale: scaleContainer => {
      const scale = new Scale();
      scale.init(scaleContainer);
      return scale;
    },
    ScreenShot: () => {
      return new ScreenShot();
    },
    CameraControl: () => {
      return new AdvancedCameraControl();
    },
    EmbedCurrentFocusInURL: () => {
      if (this.CameraControl) {
        this.CameraControl.embedCurrentFocusInURL();
      } else {
        throw new Error(
          "Feature 'EmbedCurrentCoordInURL' require 'CameraControl' to be enabled.",
        );
      }
    },
  };

  constructor(dataControllers = {}, features = {}) {
    this.viewer = window.viewer;
    this._dataControllers = {};
    this.shouldPickEntity = true;
    window.shouldPick = this.shouldPickEntity;

    // CesiumJS selection utils
    this.selectOnMap = selectOnMap;
    this.selectEntity = selectEntity;
    this.selectEntityById = selectEntityById;
    this.selectEntityFromDataSourceById = selectEntityFromDataSourceById;
    this.getEntityById = getEntityById;
    this.getRawDataById = getRawDataById;
    this.getAllEntities = getAllEntities;

    Object.keys(dataControllers).forEach(dataController => {
      this[dataController] = new dataControllers[dataController].controller(
        dataControllers[dataController].spatials,
      );
      this._dataControllers[dataController] = this[dataController];
    });
    features = { ...this._defaultFeatures, ...features };
    Object.keys(features).forEach(feature => {
      const _feature = features[feature];
      if (_feature) {
        this[feature] = this._features[feature](_feature);
      }
    });
  }

  /**
   *
   * Get all available DataController's names
   * @return {Array}
   * @memberof MapController
   */
  getDataControllerNames() {
    return Object.keys(this._dataControllers);
  }

  destroy() {
    Object.keys(this).forEach(key => {
      if (
        this[key] == undefined ||
        this[key] instanceof BaseCesiumSpatial ||
        this[key].constructor == Object ||
        key === "viewer" ||
        typeof this[key] === "function" ||
        typeof this[key] !== "object"
      ) {
        return;
      }
      try {
        this[key].destroy();
      } catch {
        console.debug(
          `${key} Does not have a destroy method, or error occured during destroy, leak may occur.`,
        );
      }
    });
  }
}

export default MapController;
