/*eslint-disable no-unused-vars */
import * as Cesium from "cesium/Cesium";
import * as turf from "@turf/turf";

import { MapController } from "../../index";

function createPoint(worldPosition, options) {
  if (!window.viewer.scene.pickPositionSupported) {
    window.alert("This browser does not support pickPosition.");
  }
  let point;
  let pointOption = {
    position: worldPosition,
    point: {
      color: Cesium.Color.MINTCREAM,
      pixelSize: 10,
      heightReference: Cesium.HeightReference.CLAMP_TO_GROUND,
    },
  };
  if (options.drawingMode == "polygon") {
    point = window.viewer.entities.add(pointOption);
  } else {
    if (options.unit == "miles") {
      pointOption["label"] = {
        text: `${options.rawRadius} 海哩`,
        font: "15px sans-serif",
        pixelOffset: new Cesium.Cartesian2(0.0, 20),
      };
    } else {
      pointOption["label"] = {
        text: `${options.rawRadius} 公尺`,
        font: "15px sans-serif",
        pixelOffset: new Cesium.Cartesian2(0.0, 20),
      };
    }
    point = window.viewer.entities.add(pointOption);
  }
  return point;
}
function drawShape(positionData, options) {
  if (!window.viewer.scene.pickPositionSupported) {
    window.alert("This browser does not support pickPosition.");
  }
  let shape;
  if (options.drawingMode == "polygon") {
    shape = window.viewer.entities.add({
      polygon: {
        hierarchy: positionData,
        material: new Cesium.ColorMaterialProperty(
          Cesium.Color.YELLOWGREEN.withAlpha(0.5),
        ),
        // clampToGround: true,
      },
    });
  } else {
    shape = window.viewer.entities.add({
      position: positionData,
      ellipse: {
        semiMinorAxis: options.newRadius,
        semiMajorAxis: options.newRadius,
        material: Cesium.Color.PALEVIOLETRED.withAlpha(0.3),
      },
    });
  }
  return shape;
}
function leftClickEvent(position, handler) {
  MapController.shouldPickEntity = false;
  handler.setInputAction(function(event) {
    const ray = window.viewer.camera.getPickRay(event.position);
    const earthPosition = window.viewer.scene.globe.pick(
      ray,
      window.viewer.scene,
    );
    const selectRaw = Cesium.Cartographic.fromCartesian(earthPosition);
    const lat = parseFloat(Cesium.Math.toDegrees(selectRaw["latitude"]));
    const lon = parseFloat(Cesium.Math.toDegrees(selectRaw["longitude"]));
    position.selectPoint.push([lon, lat]);
    window.viewer.selectedEntity = null;
    if (Cesium.defined(earthPosition)) {
      if (position.drawingMode == "polygon") {
        if (position.activeShapePoints.length === 0) {
          position.floatingPoint = createPoint(earthPosition, position);
          position.entities.push(position.floatingPoint);
          position.activeShapePoints.push(earthPosition);
          const dynamicPositions = new Cesium.CallbackProperty(function() {
            return new Cesium.PolygonHierarchy(position.activeShapePoints);
          }, false);
          position.activeShape = drawShape(dynamicPositions, position);
        }
        const point = createPoint(earthPosition, position);
        position.entities.push(point);
        position.activeShapePoints.push(earthPosition);
        window.viewer.selectedEntity = null;
      } else {
        const center = createPoint(earthPosition, position);
        const circle = drawShape(earthPosition, position);
        position.entities.push(center);
        position.entities.push(circle);
        Object.values(position.boatData).forEach(v => {
          let contain = position.booleanInCircle(
            v["position"],
            earthPosition,
            position.newRadius,
          );
          if (contain == true) {
            position.insidePoint.push({
              CTNO: v["id"],
              type: v["type"],
              gpsTime: v["gpsTime"],
              lng: v["position"][0],
              lat: v["position"][1],
            });
          }
        });
        window.viewer.selectedEntity = null;
      }
    }
  }, Cesium.ScreenSpaceEventType.LEFT_CLICK);
}
function mouseMoveEvent(position, handler) {
  handler.setInputAction(function(event) {
    if (Cesium.defined(position.floatingPoint)) {
      const ray = window.viewer.camera.getPickRay(event.endPosition);
      const newPosition = window.viewer.scene.globe.pick(
        ray,
        window.viewer.scene,
      );
      if (Cesium.defined(newPosition)) {
        position.floatingPoint.position.setValue(newPosition);
        position.activeShapePoints.pop();
        position.activeShapePoints.push(newPosition);
      }
    }
  }, Cesium.ScreenSpaceEventType.MOUSE_MOVE);
}
function rightClickEvent(position, handler) {
  handler.setInputAction(function(event) {
    position.activeShapePoints.pop();
    const polygon = drawShape(position.activeShapePoints, position);
    position.entities.push(polygon);
    window.viewer.entities.remove(position.floatingPoint);
    window.viewer.entities.remove(position.activeShape);
    position.floatingPoint = undefined;
    position.activeShape = undefined;
    position.activeShapePoints = [];
    position.polygons[polygon.id] = position.selectPoint;
    position.selectPoint.push(position.selectPoint[0]);
    if (position.boatData) {
      Object.values(position.boatData).forEach(v => {
        let contain = position.booleanInPolygon(
          v["position"],
          position.selectPoint,
        );
        if (contain == true) {
          position.insidePoint.push({
            CTNO: v["id"],
            type: v["type"],
            gpsTime: v["gpsTime"],
            lng: v["position"][0],
            lat: v["position"][1],
          });
        }
      });
    }
    position.selectPoint = [];
    MapController.shouldPickEntity = true;
    position.destroy();
    handler.destroy();
  }, Cesium.ScreenSpaceEventType.RIGHT_CLICK);
}

/**
 *
 * @description This is the rredecessor of AreaExporter, which will be deprecated soon.
 * @class PolygonDrawer
 */
class PolygonDrawer {
  constructor() {
    this.drawingMode;
    this.polygons = {};
    this.activeShapePoints = [];
    this.activeShape = undefined;
    this.floatingPoint = undefined;
    this.selectPoint = [];
    this.handler = undefined;
    this.testPoint;
    this.insidePolygon = undefined;
    this.entities = [];
    this.unit;
    this.rawRadius;
    this.radius;
    this.boatData = null;
    this.insidePoint = [];
  }
  getSelectPoint(data) {
    this.boatData = data;
  }
  getInfo() {
    if (this.insidePoint.length >= 0) {
      return this.insidePoint;
    } else {
      return 400;
    }
  }

  drawPolygon(mode) {
    if (!window.viewer.scene.pickPositionSupported) {
      window.alert("This browser does not support pickPosition.");
    }
    // window.viewer.scene.globe.depthTestAgainstTerrain = true;
    this.handler = new Cesium.ScreenSpaceEventHandler(window.viewer.canvas);
    this.drawingMode = mode;

    // LEFT_CLICK_EVENT
    leftClickEvent(this, this.handler);
    // MOUSE_MOVE_EVENT
    mouseMoveEvent(this, this.handler);
    // RIGHT_CLICK_EVENT
    rightClickEvent(this, this.handler);
  }
  drawCircle(unit, radius) {
    if (!window.viewer.scene.pickPositionSupported) {
      window.alert("This browser does not support pickPosition.");
    }
    this.handler = new Cesium.ScreenSpaceEventHandler(window.viewer.canvas);
    this.drawingMode = "circle";
    this.unit = unit;
    this.rawRadius = radius;
    if (unit == "miles") {
      this.newRadius = Math.floor(radius * 1852);
    } else {
      this.newRadius = Math.floor(radius);
    }
    // LEFT_CLICK_EVENT
    leftClickEvent(this, this.handler);
  }
  clearAll() {
    this.entities.forEach(entity => {
      window.viewer.entities.remove(entity);
    });
    window.viewer.scene.globe.depthTestAgainstTerrain = false;
    this.selectPoint = [];
    this.entities = [];
    this.polygons = {};
    this.insidePoint = [];
  }
  /**
   * @param targetpoint which point you want to check
   * @param polygon
   */
  booleanInPolygon(targetPoint, polygon) {
    let testPoint = turf.point(targetPoint);
    let polygonPoints = turf.polygon([polygon]);
    let inside = turf.booleanPointInPolygon(testPoint, polygonPoints);
    return inside;
  }
  booleanInCircle(targetPoint, circleCenter, radius) {
    const targetCartographicPoint = Cesium.Cartographic.fromDegrees(
      targetPoint[0],
      targetPoint[1],
    );
    const circleCenterCartographicPoint = Cesium.Cartographic.fromCartesian(
      circleCenter,
    );
    const ellipsoidGeodesic = new Cesium.EllipsoidGeodesic(
      targetCartographicPoint,
      circleCenterCartographicPoint,
    );
    return ellipsoidGeodesic.surfaceDistance <= radius;
  }
  destroy() {
    this.handler.removeInputAction(Cesium.ScreenSpaceEventType.LEFT_CLICK);
    this.handler.removeInputAction(Cesium.ScreenSpaceEventType.RIGHT_CLICK);
    this.handler.removeInputAction(Cesium.ScreenSpaceEventType.MOUSE_MOVE);
  }
}
export default PolygonDrawer;
